export default function Spell() {
    const [spells, setSpells] = useState([]);
    const [openIndex, setOpenIndex] = useState(null);

    useEffect(() => {
        getAllSpells().then(setSpells);
    }, []);

    return (
        <div className="font-serif text-center">
            {spells.length === 0 && <span className="text-amber-400">Loading...</span>}
            <ul className="flex flex-wrap justify-center gap-16">
                {spells.map((spell) => (
                    <SpellCard key={spell.index} spell={spell}
                        isOpen={openIndex === spell.index} onToggle={() => 
                        setOpenIndex(openIndex === spell.index ? null : spell.index)
                        }
                    />
                ))}
            </ul>
        </div>
    );
}
